export default {
	data() {
		return {
			idx:"-1",
			list:[],
			priceArea:'222',
			listItem:'',
			flag:false,
			
		};
	},
	methods:{
		// 选择类别
		select(index,item){
			this.idx = index;
			this.listItem = item;
			this.$emit('priceArea',this.idx,this.priceArea,item)
		},
		// 输入框失去焦点
		onBlur(){
			this.flag = false;
			this.$emit('priceArea',this.idx,this.priceArea,this.listItem);
			if(this.priceArea !== ''){
				console.log(this.priceArea.length)
				/* if(this.priceArea.length >= 5){
					this.flag = true;
				} */
				let regex=/^[0]+/;
				this.priceArea=this.priceArea.replace(regex,'');
				if(this.priceArea == ''){
					this.priceArea = 0;
				}
				this.priceArea = this.priceArea + 'm²';
			}
		},
		// 输入框获得焦点
		onFocus(e){
			// this.flag = false;
			console.log(e.detail)
			/* if(this.priceArea.length == 6){
				this.flag = true;
			} */
			if(this.priceArea !== ''){
				this.priceArea = this.priceArea.slice(0,this.priceArea.indexOf('m²'));
			}
			if(this.priceArea.length == 5){
				this.flag = true;
			}
			console.log(this.priceArea.length)
		},
		// 判断用户输入的内容只能4位
		/* onInput(e){
			console.log(e.detail)
			if(this.priceArea.length >= 4){
				uni.showModal({
				    title: '输入的内容不能超过4位',
					showCancel:false,
					success: res=>{
						if(res.confirm){
							console.log(this.priceArea.length)
						}
					}
				});
				this.flag = true;
			}
		} */
	},
	mounted(){
		this.$emit('priceArea',this.idx,this.priceArea);
		let categorys;
		if (uni.getStorageSync("catalogues")) {
			categorys = uni.getStorageSync("catalogues").categorys;
		} else if (this.$store.state.catalogues) {
			categorys = this.$store.state.catalogues.categorys;
		}
		if (categorys && categorys.length > 0) {
			let list = categorys.filter((item)=>{
				return item.type == 1;
			})
			this.list = list;
			// this.list.pop();
		}
	}
}
